let FormateDate = function(date) {
  let o = {
    'yy':date.getFullYear(),
    'MM': date.getMonth() + 1,
    'dd': date.getDate(),
    'HH': date.getHours(),
    'mm': (()=>{
      let mm = date.getMinutes()
      if(mm<10){
        mm = '0' + mm
      }
      return mm
    })(),
    'ss': date.getSeconds()
  };
  return o.yy+'-'+o.MM+'-'+o.dd+' '+o.HH+':'+o.mm+':'+o.ss;
};
